.std_form-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.std_form-modal-box {
  background-color: white;
  padding: 2rem;
  border-radius: 12px;
  text-align: center;
  max-width: 400px;
  width: 90%;
  animation: fadeIn 0.3s ease-in-out;
}

.std_form-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #006400;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.std_form-close {
  margin-top: 20px;
  background-color: crimson;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

@keyframes fadeIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
