.sectionVerifyCBTExams {
  min-height: 100vh;
  background-image: url("./test.jpeg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.cbtCourseSelect {
  padding: 1rem;
  background-color: #f0f0f0;
  border-radius: 0.7rem;
  cursor: pointer;
}

.cbtCourseSelect.checked {
  padding: 1rem;
  background-color: #6de78c;
  border-radius: 0.7rem;
  outline: 1px solid #26bf89;
}

.cbtCourseSelect:hover,
.cbtCourseSelect:active {
  padding: 1rem;
  background-color: #e0e0e0;
  border-radius: 0.7rem;
}
